body {
  height: 100vh;
  width: 100vw;
  background-image: url("./refs/paper_texture_11.png");
  background-size: cover;
  font-family: 'Cormorant Garamond', serif;
}

.ant-page-header-heading-extra {
  .ant-btn-text {
    font-size: 18px;
    height: 40px;
  }
}

#element {
  height: 300px;
  width: 400px;
}



#root {
  width: 100%;
  height: 100%;
}

.page {
  width: 100%;
  height: calc(100% - 80px);
  display: flex;
  flex-direction: column;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 24px;
  .title {
    font-size: 90px;
  }
  .description {
    width: 70%;
    font-size: 30px;
  }
  .image {
    width: 300px;
    height: 300px;
    background-size: cover;
    background-image: url("./refs/technology.d86bf2e5.png");
    align-self: flex-end;
  }
  .footer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    .active {
      text-decoration: underline;
    }
  }
}